<template>
  <app-header />
  <div class="home">
    <div class="alert alert-success alert-dismissible fade show" role="alert">
      هذه الصفحة فارغة ( تحت الإنشاء )<br />
      سوف يتم تحويلك الى صفحة اخرى خلال خمس ثواني
    </div>
    {{ timer }}
  </div>
</template>

<script>
  import AppHeader from "@/components/gloabl/AppHeader.vue";
  import firebase from "firebase";
  import SayHellow from "@/components/gloabl/SayHellow.vue";
  export default {
    name: "Home",
    data() {
      return {
        data() {
          return {
            disName: null,
          };
        },
        created() {
          var user = firebase.auth().currentUser;
          this.disName = user.displayName;
        },
      };
    },
    components: {
      AppHeader,
      SayHellow,
    },
    computed: {
      timer() {
        setTimeout(() => {
          this.$router.push("/blog");
        }, 5000);
      },
    },
  };
</script>

<style lang="scss" scoped>
  .show {
    font-size: 30px;
  }
</style>
